import React, {Component} from 'react';
import {Background} from './App.styled';
import NavigationBar from '../../components/Navigator/NavigationBar.js'

class App extends Component {
    render() {
        return (
            <div>
                <Background/>
                <NavigationBar/>
                {this.props.children}
            </div>
        );
    }
}

export default App;



