import styled from 'styled-components';
import {lightblue,bluegreen,maybe } from '../../theme/Color';




export const ImageButton = styled.div`
cursor : pointer;
overflow: hidden;

&>img{
transition: transform .66s
}

&: hover {
  & > img{
  transform: scale(1.3);
  }
}

`;

export const Index = styled.div`
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: -1;
  h1{
  font-size :5em;
  margin: 0;
 font-weight: bold;
  color: ${lightblue};
  opacity:0.8;
  transform: translate{40%};
  }

`;

export const Index2 = styled.div`
  position: absolute;
  left: 0;
  bottom:0;
  z-index: -1;
  h1{
  font-size: 5em;
  margin: 0;
  font-weight:bold;
  color: ${bluegreen};
  opacity: 0.8;
  transform: translate{40%};
  }
`;

export const ImageContainer = styled.div`
width: 800px;
margin-left:auto;
margin-right: auto;
`;

export const Title = styled.h1`
color : ${maybe};
font-weight: bold;
margin-top: 0;
display:inline-block;
`;



