import React, {Component} from 'react';
import {
    Container,
    Relative,
    Flix,
} from '../../theme/grid';
import Zoomy from 'react-zoomy';
import {
    ImageButton,
    Index,
    ImageContainer,
    Index2,
    Title
} from '../Project/Project.styled.js';
import {
    A,
    B
} from '../../theme/typo';
import ParallaxImage from 'react-image-parallax2';

class Project extends Component {
    render() {
        return (
            <Container>
                <Title>My projects</Title>
                <Relative marginBottom="50px" marginTop="100px">
                    <Index>
                        <h1>01</h1>
                    </Index>
                    <h1>My first <A href="">London-fog</A>
                    </h1>
                </Relative>
                <Flix justify={'center'} marginBottom="5em">
                    <Zoomy
                        imageUrl={require('../../asserts/dark.jpg')}
                        renderThumbnail={({showImage}) =>
                            <ImageButton onClick={showImage}>
                                <img
                                    src={require('../../asserts/dark_small.jpg')}
                                    alt="dark_s"/>
                            </ImageButton>
                        }
                        scale={[1.1, 1.1]
                        }
                        imageProps={{
                            style: {
                                width: '100vw',
                                height: 'auto'
                            }
                        }}/>
                </Flix>
                <Relative marginBottom="100px">
                    <Index2>
                        <h1>02</h1>
                    </Index2>
                    <h1>My second <B href="">Second-project</B></h1>
                </Relative>
                <ImageContainer>
                    <ParallaxImage
                        reduceHeight={1 / 3}
                        src={require('../../asserts/dark-cover.jpg')}/>
                </ImageContainer>
            </Container>
        );
    }
}

export default Project;


